@use 'sass:map';
@use "../abstracts" as a;
.latest-articles {
  padding: 2em;
  margin-bottom: 3rem;

  @include a.mq(map.get(a.$break_points, "tablet")) {
    padding: 1em;
  }
  &__title {
    font-size: 2rem;
    font-weight: 400;
    text-align: center;
    padding-bottom: 2rem;
    @include a.mq(map.get(a.$break_points, "tablet")) {
      text-align: left;
    }
  }
  &__cards {
    @include a.mq(map.get(a.$break_points, "tablet")) {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(16rem, 1fr));
      grid-gap: 1em;
    }
    .card {
      border-radius: 5px;
      overflow: hidden;
      margin: 0 auto;
      margin-bottom: 3em;
      background-color: map.get(a.$color, "white");
      @include a.mq(map.get(a.$break_points, "tablet")) {
        margin: 0;
        flex: 100%;
      }

      &__img {
        padding-bottom: 2rem;
        overflow: hidden;
        .img {
          min-height: 15rem;
          height: 15vw;
          max-height: 5rem;
          width: 100%;
          display: block;
          object-fit: cover;
        }
      }
      &__body {
        padding: 0 1.5rem;
        max-width: 23rem;
        @include a.mq(map.get(a.$break_points, "tablet")) {
          max-width: 16rem;
        }
      }
      &__username {
        font-size: 0.75rem;
        padding-bottom: 0.5rem;
        display: block;
      }
      &__title {
        font-weight: 400;
        font-size: 1.1rem;
        padding-bottom: 0.5rem;
        transition: color 200ms ease-in-out;
        &:hover {
          color: map.get(a.$color, "Lime-green");
          cursor: pointer;
        }
      }
      &__text {
        font-size: 0.875rem;
        padding-bottom: 1rem;
        line-height: 1.3;
      }
    }
  }
}
