@use '../abstracts' as a;
@use "sass:map";
.button {
  background-image: linear-gradient(
    to right,
    map.get(a.$color, "Lime-green"),
    map.get(a.$color, "Bright-cyan") 70%
  );
  color: #fff;
  padding: 0.8em 2em;
  border-radius: 2em;
  display: inline-block;
  transition: opacity 200ms ease-in-out;
  &:hover {
    opacity: 0.7;
  }
}
