@use '../abstracts' as a;
@use "sass:map";
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  overflow-x: hidden;
}
body {
  color: map.get(a.$color, "Grayish-blue");
  font-family: Public Sans, sans-serif;
  font-size: 1.125rem;
  background-color: map.get(a.$color, "Very-light-gray");
}
h1,
h2,
h3 {
  color: map.get(a.$color, "Dark-blue");
}
