@use 'sass:map';
@use "../abstracts" as a;

.header {
  position: relative;
  background-color: map.get(a.$color, "White");
  padding: 0 1rem;
  .overlay {
    content: "";
    position: absolute;
    transition: visibility 400ms ease-in-out, opacity 400ms ease-in-out;
    visibility: hidden;
    opacity: 0;
    z-index: 2;
    background-image: linear-gradient(
      to bottom,
      map.get(a.$color, "Dark-blue"),
      transparent
    );
    top: 100%;
    left: 0;
    height: calc(100vh - 100%);
    width: 100%;
    &.opened {
      visibility: visible;
      opacity: 1;
      @include a.mq(map.get(a.$break_points, "desktop")) {
        display: none;
      }
    }
  }
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    position: relative;
    padding: 1em 0;

    .nav__list {
      z-index: 2;
      position: absolute;
      font-size: 1rem;
      text-transform: capitalize;
      background-color: map.get(a.$color, "White");
      margin-top: 2em;
      padding: 2em 0;
      left: 50%;
      top: 100%;
      width: 60%;
      max-width: 350px;
      min-width: 200px;
      transform: translate(-50%, -10%);
      opacity: 0;
      visibility: hidden;
      text-align: center;
      margin: 2em auto;
      font-weight: 700;
      border-radius: 5px;
      transition: visibility 200ms ease-in-out, opacity 200ms ease-in-out,
        transform 200ms ease-in-out;
      &.opened {
        transform: translate(-50%, 0);
        opacity: 1;
        visibility: visible;
        @include a.mq(map.get(a.$break_points, "desktop")) {
          transform: translate(0);
        }
      }
      @include a.mq(map.get(a.$break_points, "desktop")) {
        position: static;
        visibility: visible;
        opacity: 1;
        transform: translate(0%);
        width: auto;
        padding: 0;
        margin: 0;
        display: flex;
        gap: 2em;
        position: static;
      }
      .nav__item {
        .nav__link {
          padding: 0.5em 0;
          transition: background-color 1000ms ease-in-out,
            color 500ms ease-in-out;
          &:hover {
            background-color: lighten(map.get(a.$color, "Grayish-blue"), 25%);

            color: map.get(a.$color, "Dark-blue");
            transition: background-color 100ms ease-in-out,
              color 100ms ease-in-out;
          }
        }
        @include a.mq(map.get(a.$break_points, "desktop")) {
          padding-bottom: 1em;
          position: relative;
          transition: color 200ms ease-in-out;
          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 100%;
            width: 100%;
            height: 3px;
            background-color: map.get(a.$color, "Lime-green");
            transform: scaleX(0);
            transform-origin: left;
            transition: transform 200ms ease-in-out;
          }
          &:hover::after {
            transform: scaleX(1);
          }
          .nav__link:hover {
            background-color: transparent;

            color: map.get(a.$color, "Dark-blue");
          }
        }
      }
    }
    .nav__toggle {
      height: auto;
      cursor: pointer;

      span {
        display: block;
        width: 1.625rem;
        height: 2px;
        transform-origin: 3px 1px;
        background-color: map.get(a.$color, "Dark-blue");
        transition: transform 400ms ease-in-out, opacity 400ms ease-in-out;
        &:not(:last-child) {
          margin-bottom: 5px;
        }
      }
      &.opened span {
        &:nth-child(1) {
          transform: rotateZ(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:nth-child(3) {
          transform: rotateZ(-45deg);
        }
      }
      @include a.mq(map.get(a.$break_points, "desktop")) {
        display: none;
      }
    }
    &__cta {
      display: none;
      @include a.mq(map.get(a.$break_points, "desktop")) {
        display: inline-block;
      }
    }
  }
}
