@use 'sass:map';
@use "../abstracts" as a;

.footer {
  background-color: map.get(a.$color, "Dark-blue");
  text-align: center;
  padding: 2rem 1rem;
  .container {
    @include a.mq(map.get(a.$break_points, "desktop")) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      text-align: left;
    }
  }
  &__intro-wrapper {
    @include a.mq(map.get(a.$break_points, "desktop")) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex: 1;
      align-self: stretch;
    }
  }
  &__intro {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-self: stretch;
    flex: 1;
  }
  .logo {
    padding-bottom: 2rem;
    svg {
      #text {
        fill: #fff;
      }
    }
  }

  .social-media {
    padding-bottom: 2rem;

    @include a.mq(map.get(a.$break_points, "desktop")) {
      align-self: flex-start;
    }
    &__list {
      display: flex;
      justify-content: center;
    }

    @include a.mq(map.get(a.$break_points, "desktop")) {
      padding: 0;
    }

    &__item {
      &:not(:last-child) {
        margin-right: 1rem;
      }
    }
  }

  .quick-links {
    padding-bottom: 2rem;
    flex: 1;
    @include a.mq(map.get(a.$break_points, "desktop")) {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__list {
      &:not(:last-child) {
        margin-bottom: 1rem;
        @include a.mq(map.get(a.$break_points, "desktop")) {
          margin: 0;
        }
      }
    }
    &__item {
      &:not(:last-child) {
        margin-bottom: 1rem;
        @include a.mq(map.get(a.$break_points, "desktop")) {
          margin-bottom: 0.5rem;
        }
      }
    }
    &__link {
      color: map.get(a.$color, "White");
      font-size: 1rem;
      text-transform: capitalize;
      transition: color 200ms ease-in-out;
      &:hover {
        color: map.get(a.$color, "Lime-green");
      }
    }
  }
  &__outro {
    @include a.mq(map.get(a.$break_points, "desktop")) {
      text-align: right;
      flex: 1;
    }
  }
  &__cta {
    margin-bottom: 2rem;

    &:hover {
      background-image: linear-gradient(
        to right,
        lighten(map.get(a.$color, "Lime-green"), 10%),
        lighten(map.get(a.$color, "Bright-cyan"), 10%) 70%
      );
      opacity: 1;
    }
  }
  .legal-text {
    display: block;
    padding-bottom: 1rem;
    font-size: 1rem;
    text-transform: capitalize;
  }
}
