@use 'sass:map';
@use "../abstracts" as a;
.hero {
  position: relative;
  background: map.get(a.$color, "Very-light-gray");
  padding-bottom: 3em;
  .container {
    @include a.mq(map.get(a.$break_points, "desktop")) {
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 35rem;
    }
  }
  &__img {
    background-image: url("../images/bg-intro-mobile.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 22rem;
    margin-bottom: 2em;
    position: relative;

    @include a.mq(map.get(a.$break_points, "tablet")) {
      min-height: 30rem;
    }
    @include a.mq(map.get(a.$break_points, "desktop")) {
      min-height: 35rem;
      flex: 2;
      order: 2;
      background-image: none;
    }
    &::before {
      @include a.mq(map.get(a.$break_points, "desktop")) {
        content: "";
        position: absolute;
        background-image: url("../images/bg-intro-desktop.svg");
        background-repeat: no-repeat;
        background-size: 1100px;
        background-position: 95px -250px;
        width: 120%;
        height: 100%;
        right: -150px;
      }
    }
    &::after {
      content: "";
      position: absolute;
      background-repeat: no-repeat;
      background-size: 98%;
      background-position: center -8rem;
      height: 100%;
      width: 100%;
      top: 0;
      background-image: url("../images/image-mockups.png");
      @include a.mq(map.get(a.$break_points, "tablet")) {
        background-position: center center;
        background-size: 392px;
      }
      @include a.mq(map.get(a.$break_points, "desktop")) {
        position: absolute;

        background-repeat: no-repeat;
        background-size: 85%;
        background-position: center -6.6rem;
        height: 140%;
        width: 100%;
        right: -30%;
        top: 0;
        bottom: 10%;
      }
    }
  }

  &__content {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 4rem;
    padding: 0 1rem;
    @include a.mq(map.get(a.$break_points, "tablet")) {
      text-align: left;
      margin: 0;
      max-width: 40.8125rem;
    }
    @include a.mq(map.get(a.$break_points, "desktop")) {
      flex: 1;
    }
  }
  &__title {
    font-size: 2.3125rem;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 1rem;
  }
  &__body {
    margin-bottom: 1rem;
    line-height: 1.5;
  }
}
