@use 'sass:map';
@use "../abstracts" as a;

.second-section {
  text-align: center;
  padding: 1em;
  margin-bottom: 6rem;
  background-color: map.get(a.$color, "Very-light-gray");
  background-color: map.get(a.$color, "Light-grayish-blue");
  @include a.mq(map.get(a.$break_points, "tablet")) {
    text-align: left;
  }
  &__intro {
    padding-bottom: 3em;
    max-width: 20rem;
    margin: 0 auto;
    @include a.mq(map.get(a.$break_points, "tablet")) {
      max-width: 35rem;
      margin: 0;
    }
  }
  &__title {
    font-weight: 400;
    font-size: 2rem;
    padding-bottom: 0.5em;
  }
  &__subtitle {
    font-size: 1.125rem;
    line-height: 1.5;
  }
  .cards-wrapper {
    display: flex;
    flex-wrap: wrap;

    .card {
      padding-bottom: 2em;
      flex-basis: 100%;
      max-width: 16.3rem;
      margin: 0 auto;

      @include a.mq(map.get(a.$break_points, "tablet")) {
        margin: 0;

        &:not(:last-child) {
          margin-right: 1.5em;
        }
      }
      &__icon {
        padding-bottom: 1em;
      }
      &__title {
        font-weight: 400;
        font-size: 1.5rem;
        padding-bottom: 1em;
      }
      &__body {
        font-size: 1rem;
        line-height: 1.5;
      }
    }
  }
}
